.menu-nav {
    width: 95%;
    height: 40px;
    max-width: 446px;
    margin: 0 auto 8px;
    /* border-bottom: solid 1px gray; */
  }
  
  .menu-ul {
    display: flex;
    margin: 0 auto;
    justify-content: space-around;
    list-style: none;
  }
  
  .menu-ul li {
    width: 90px;
    text-align: center;
    background: transparent;
    color: #fafafa;
    cursor: pointer;
    border: none;
  }
  
  li[active] {
    /* border-bottom: solid #61dafa 1px; */
    background: #9e3cff;
    border: 2px solid;
  }

  .option-menu{
    place-content: center;
  }
  